<script setup lang="ts">
import { ref } from 'vue'
import { ChevronRight } from 'lucide-vue-next'
import type { Tab } from '@/types'

const props = defineProps<{
  tabs: Tab[]
}>()

const activeTabId = ref(props.tabs[0]?.id)

const setActiveTab = (id: string) => {
  activeTabId.value = id
}
</script>

<template>
  <div class="flex w-full h-full bg-white rounded-2xl overflow-hidden shadow-sm border border-slate-200">
    <!-- Sidebar -->
    <div class="w-48 flex-shrink-0 bg-slate-50 border-r border-slate-100 flex flex-col">
      <div 
        v-for="tab in tabs" 
        :key="tab.id"
        class="relative group"
      >
        <button
          @click="setActiveTab(tab.id)"
          @mouseenter="setActiveTab(tab.id)" 
          class="w-full text-left px-6 py-4 transition-all duration-200 hover:bg-white focus:outline-none"
          :class="activeTabId === tab.id ? 'bg-white' : ''"
        >
          <div class="flex items-center justify-between">
            <span 
              class="font-medium transition-colors"
              :class="activeTabId === tab.id ? 'text-blue-700' : 'text-slate-600 group-hover:text-slate-800'"
            >
              {{ tab.label }}
            </span>
            <ChevronRight 
              v-if="activeTabId === tab.id" 
              class="w-4 h-4 text-blue-500" 
            />
          </div>
          <p 
            v-if="tab.description" 
            class="text-xs mt-1 transition-colors"
            :class="activeTabId === tab.id ? 'text-blue-400' : 'text-slate-400'"
          >
            {{ tab.description }}
          </p>
        </button>
        
        <!-- Active Indicator Line -->
        <div 
          v-if="activeTabId === tab.id"
          class="absolute left-0 top-0 bottom-0 w-1 bg-blue-600 rounded-r-full"
        ></div>
      </div>
    </div>

    <!-- Content Area -->
    <div class="flex-1 p-0 overflow-hidden relative">
      <transition
        mode="out-in"
        enter-active-class="transition ease-out duration-200"
        enter-from-class="opacity-0 translate-y-2"
        enter-to-class="opacity-100 translate-y-0"
        leave-active-class="transition ease-in duration-150"
        leave-from-class="opacity-100 translate-y-0"
        leave-to-class="opacity-0 translate-y-2"
      >
        <div :key="activeTabId" class="w-full h-full">
          <slot :active-tab="activeTabId"></slot>
        </div>
      </transition>
    </div>
  </div>
</template>
